import { listCollect } from '@/api/business/user';
import Pagination from '@/components/Pagination'; // Secondary package based on el-pagination

export default {
  name: 'Collect',
  components: { Pagination: Pagination },
  data: function data() {
    return {
      list: null,
      total: 0,
      listLoading: true,
      listQuery: {
        page: 1,
        limit: 20,
        userId: undefined,
        valueId: undefined,
        sort: 'add_time',
        order: 'desc'
      },
      downloadLoading: false
    };
  },
  created: function created() {
    this.getList();
  },

  methods: {
    getList: function getList() {
      var _this = this;

      this.listLoading = true;
      listCollect(this.listQuery).then(function (response) {
        _this.list = response.data.data.items;
        _this.total = response.data.data.total;
        _this.listLoading = false;
      }).catch(function () {
        _this.list = [];
        _this.total = 0;
        _this.listLoading = false;
      });
    },
    handleFilter: function handleFilter() {
      this.listQuery.page = 1;
      this.getList();
    },
    resetForm: function resetForm() {
      this.dataForm = {
        id: undefined,
        userId: '',
        valueId: '',
        addTime: undefined
      };
    },
    handleDownload: function handleDownload() {
      var _this2 = this;

      this.downloadLoading = true;
      import('@/vendor/Export2Excel').then(function (excel) {
        var tHeader = ['用户ID', '商品ID', '添加时间'];
        var filterVal = ['userId', 'valueId', 'addTime'];
        excel.export_json_to_excel2(tHeader, _this2.list, filterVal, '用户收藏信息');
        _this2.downloadLoading = false;
      });
    }
  }
};